//MOVIE Card
.movie-card{
    font-family: 'Dongle', sans-serif;
    font-size: 16pt;
    cursor: pointer;
    margin: 10px;
    padding-left: 7px;
    padding-right: 7px;
    border: 0px outset black;
    height: 25px;
    width: fit-content;
    color: rgb(184, 122, 8);
    background-color: rgb(44, 38, 38);
    border-radius: 14px;
    box-shadow: 1px 1px 3px black;
    transition: .6s ease-in-out;
}

.movie-card:hover{
    font-family: 'Dongle', sans-serif;
    font-size: 16pt;
    cursor: pointer;
    margin: 10px;
    padding-left: 7px;
    padding-right: 7px;
    border: 0px outset black;
    height: 25px;
    width: fit-content;
    color: rgb(184, 122, 8);
    background-color: rgb(44, 38, 38);
    border-radius: 14px;
    box-shadow: 1px 1px 3px black,
                2px 2px 3px black,
                3px 3px 4px rgba(58, 57, 57, 0.842);
}
button{
    cursor: pointer;
    border: 0px outset silver;
    font-family: 'Dongle', sans-serif;
    color: chartreuse;
    background-color: rgb(14, 13, 13);
    margin: 7px;
    width: 75px;
    margin-left: 40%;
    border-radius: 14px;
    box-shadow: 1px 1px 3px black,
                2px 2px 3px black,
                3px 3px 4px rgba(15, 15, 15, 0.808);
}
img{
    width: 250px;
    height: 300px;;
}
.card{
    background-color: black;
}

.card-img-top{
    background-color: black;;
}

.card-body{
    background-color: black;
}